import { customElement, property } from 'lit/decorators.js';
import { css, CSSResultGroup, html, LitElement, TemplateResult } from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import './zwc-avatar';

@customElement('zwc-avatar-info')
export class ZwcAvatarInfo extends LitElement {
  @property({ type: String, attribute: 'background-color', reflect: true })
  backgroundColor = '';

  @property({ type: String })
  color = '';

  @property({ type: String })
  title = '';

  @property({ type: String })
  subtitle = '';

  @property({ type: String })
  size = 'sm';

  @property({ type: Boolean })
  outline = false;

  @property({ type: String })
  orientation: 'horizontal' | 'vertical' = 'horizontal';

  static override styles?: CSSResultGroup | undefined = [
    css`
      :host {
        height: fit-content;
        width: fit-content;
        display: block;
      }

      .avatar-info {
        position: relative;
        cursor: pointer;
        display: inline-flex;
        text-decoration: none;
      }

      .avatar-info:hover .avatar-title {
        color: var(--c-zurichblue);
      }

      .avatar-vertical {
        flex-direction: column;
        text-align: center;
        align-items: center;
      }

      .avatar-vertical zwc-avatar {
        margin-bottom: var(--spc-2);
      }

      .avatar-vertical .avatar-basic {
        margin-bottom: var(--spc-2);
      }

      .avatar-vertical .avatar-content {
        margin-left: 0;
      }

      .avatar-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: var(--spc-3);
      }

      .avatar-title {
        color: var(--c-zurichblue);
        font-size: var(--font-size-base-sm);
        line-height: 19px;
        font-family: var(--f-sans-regular);
      }

      .avatar-subtitle {
        color: var(--c-grey-07);
        font-size: 12px;
        font-family: var(--f-sans-medium);
      }

      .avatar-info .avatar-basic {
        background-color: var(--c-zurichblue);
        color: white;
      }

      @media only screen and (max-width: 600px) {
        .avatar-title {
          font-size: var(--font-size-root);
        }
      }
    `,
  ];

  protected override render(): TemplateResult {
    const classes = {
      'avatar-vertical': this.orientation === 'vertical',
    };

    return html`
      <div class="avatar-info ${classMap(classes)}">
        <zwc-avatar
          size=${this.size}
          background-color="${this.backgroundColor}"
          color="${this.color}"
          ?outline="${this.outline}"
        >
          <slot name="icon"></slot>
          <slot name="image"></slot>
          <slot></slot>
        </zwc-avatar>
        <div class="avatar-content">
          <span class="avatar-title">${this.title}</span>
          <span class="avatar-subtitle">${this.subtitle}</span>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-avatar-info': ZwcAvatarInfo;
  }
}
